.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.QRcam{
	margin: 0 auto;
  padding: 10px;
  display: block;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.select-room {
  width: 100%;
  padding-top: 1rem;
}

.cart-btns {
  padding-right: 16px;
}

.dropdown-custom {
  margin-bottom: 1rem;
}

.item-list {
  color: white;
}

.item-cart {
  padding: 1rem;
  padding-bottom: 0;
}

.jumbotron-qr {
  width: 100%;
  height: auto;
  background: none;
  background-color: white;
  margin-top: 1rem;
  padding: 0 16px 16px 16px;
}

.hr-black {
  border-color: black;
  height: 3px;
  width: 75%;
}

.submission-state {
  color: white;
}

ol > ol {
  color: black;
}